body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
 
}

 
#flex-container {
  display: grid;
  grid-template-columns: 67.5% 27.5%;
  grid-column-gap: 5%;
  
  margin: 0;
 
 
}

.parent{
  padding: 0 7vw;
}
.VidWrapper{
  margin-left: 15px;
  position: relative;
  width: 100%;
  height: 55vh;
  display:inline-block;
  background-color:cyan;
  background-image: url("https://i.pinimg.com/originals/a3/8c/ba/a38cbae225a597dd16be170c17a65157.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.song{
  white-space: nowrap; 
  width: 300px; 
  overflow: hidden;
  text-overflow: ellipsis; 

}
#queue{
  padding:2px 8px 8px 8px;
  width: 100%;
  box-shadow: rgb(187,187,1870) 0rem 0.125rem 0.5rem;
 

}

#chat{
  padding:2px 8px 8px 2px;
  width: 25vw;
  box-shadow: rgb(187,187,1870) 0rem 0.125rem 0.5rem;
 
}

.Chat_log{

  min-width:22vw;
  padding :0.5rem;
  margin : 1rem;
  min-height :40vh;
  font-size: 1.2rem;
  border :  0.005rem solid grey;
}





#chat-message-input{
  margin-left: 14px;
}
#search {
  margin: 3em auto; }
  #search .input,
  #search .transition {
    width: 100%; }

.home {
  padding: 0;
  margin: 0; }

.back_img {
  background-image: url(/static/media/background.36f3067e.jpg);
  background-repeat: no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  z-index: -140; }
  .back_img::after {
    background-color: rgba(0, 0, 0, 0.8);
    content: "         ";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -130; }

.home h1 {
  color: white;
  padding: 10rem 0 0 0;
  font-size: 3.5rem; }

.home button {
  padding: 1rem 2rem;
  font-size: 2rem;
  color: white;
  border: 1px solid white;
  background: none;
  margin: 20vh 0 0 0;
  cursor: pointer;
  border-radius: 4px; }

